<template>
  <div id="loading">
    <div class="loading_box" v-if="showLoading">
      <img src="../../../static/images/loading.png"/>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    name: 'loading',
    props: {
      showLoading: {
        type: Boolean,
        default: false
      }
    }
  }
</script>
<style>
  #loading {
    width: 100%;
  }
  #loading .loading_box {
    position: fixed;
    top:78px ;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: rgba(0, 0, 0, 0.5); */
    z-index: 100000;
  }
  #loading .loading_box img {
    display: block;
    width:50px;
    height: 50px;
    animation: rotating 1s linear infinite;
  }
  @keyframes rotating {
    from { transform: rotate(0deg) } to { transform: rotate(360deg) }
  }
</style>



